// src/models/User.js
import mongoose from "mongoose";

const userSchema = new mongoose.Schema({
  name: { type: String },
  email: { type: String, index: true, sparse: true, unique: false },
  phone: { type: String, index: true, sparse: true, unique: false },
  password: { type: String }, // hashed password (bcrypt)
  oauthProvider: String,
  oauthId: String,
  role: { type: String, enum: ["CUSTOMER","RETAILER","WHOLESALER"], default: "CUSTOMER" },
  verified: { type: Boolean, default: false },
  otp: {
    code: String,
    expiresAt: Date
  }
}, { timestamps: true });

export default mongoose.models.User || mongoose.model("User", userSchema);
