// src/pages/api/auth/login.js
import dbConnect from "../../../lib/dbConnect";
import User from "../../../models/User";
import bcrypt from "bcryptjs";
import jwt from "jsonwebtoken";

export default async function handler(req, res) {
  await dbConnect();
  if (req.method !== "POST") return res.status(405).end();

  const { email, phone, password } = req.body || {};
  if (!password || (!email && !phone)) {
    return res.status(400).json({ ok: false, message: "email or phone and password required" });
  }

  const query = email ? { email } : { phone };
  const user = await User.findOne(query);
  if (!user || !user.password) {
    return res.status(401).json({ ok: false, message: "Invalid credentials" });
  }

  const ok = await bcrypt.compare(password, user.password);
  if (!ok) return res.status(401).json({ ok: false, message: "Invalid credentials" });

  const secret = process.env.JWT_SECRET;
  if (!secret) return res.status(500).json({ ok: false, message: "Server misconfigured" });

  const token = jwt.sign({ id: user._id.toString(), role: user.role }, secret, { expiresIn: "30d" });

  return res.json({
    ok: true,
    message: "Logged in",
    token,
    user: { id: user._id.toString(), name: user.name, email: user.email, phone: user.phone, role: user.role }
  });
}
